import React from "react";
import "../CSS/Header.css";
import { Link } from "react-router-dom";
const Header = () => {
  return (
    <>
      <div className="row mainRow">
        <div className="col-3 mt-1">
          <Link
            to="/dashboard"
            className="logoSet align-items-center mb-3 mb-md-0 me-md-auto text-decoration-none"
          ><span>
              <img src={require("../images/logo-header.svg").default} />
            </span>
            <img src={require("../images/LogoName.svg").default} />
          </Link>
        </div>

        <div className="col-4">
          <div className="headerLinks">
            <a
              aria-current="page"
              href="/addCustomer"
              style={{ backgroundColor: "none" }}
            >
              <button className="btn btn-1">
                Add customer
              </button>

            </a>

            <a className="" href="/addCandidate">
              <button className="btn btn-2">ADD CANDIDAT</button>

            </a>
          </div>
        </div>

        <div className="col-5">
          <div className="text-end">
            <a
              href="https://www.intermann.ro/"
              target="_blank"
            >
              <button className="btn-003">VOIR LE SITE EN ROUMAIN</button>
            </a>
            <a
              href="https://www.intermann.fr/"
              target="_blank"
              className="ml-1"
            >
              <button className="btn-004">VOIR LE SITE EN FRANçAIS</button>
            </a>
          </div>

        </div>
      </div>
    </>
  );
};
export default Header;
