import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import '../CSS/Sidebar.css';
import Header from "./Header";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { Toaster, toast } from 'react-hot-toast';
import { logout } from '../redux/actions/userActions';
import $ from 'jquery'
function Sidebar(props: any) {

  const dispatch = useDispatch();
  const navigate = useNavigate()
  const LogNotify = () => toast.success("Log-Out!");

  const LogOut = async () => {
    await dispatch(logout())
    await localStorage.removeItem("token")
    navigate('/')
    LogNotify()
  }

  useEffect(() => {
    $(".nav-link").on('click', 'li', function () {
      $(this).addClass('active').siblings().removeClass('active')
    })
  })

  return (
    <>
      <div className="container-fluid" style={{ height: "100%", backgroundColor: "white" }}>

        {/* Header Div - row > col-12 */}
        <div className="row" >
          <div className="col-12">
            <Header />
            {/* <section style={{ marginTop: "60px" }}>
              {props.children}
            </section > */}
          </div>
        </div>

        <div className="row mt-3 sidebarDiv">
          {/* Sidebar Div - row > col-3 */}
          <div className="col-2 mt-2 fixed pd-gutter" style={{ height: "100vh", minWidth: '270px', backgroundColor: 'white' }}>
            <div
            >
              <ul className="mb-auto list-group">
                <li className="mt-1 list-gourp-item">
                  <Link to="/dashboard" className="link-dark  pd013" aria-current="page">
                    <span className="pe-2">
                      <img src={require("../images/Shape.svg").default} />
                    </span>
                    Resume
                  </Link>
                </li>
                <li className="accordion mt-1 accordion-flush" id="accordionFlushExample">
                  <div className="accordion-item">
                    <h2 className="accordion-header" id="flush-headingTwo">
                      <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#flush-collapseTwo" aria-expanded="false" aria-controls="#flush-collapseTwo">
                        <span className="pe-2">
                          <img src={require("../images/CombinedShape.svg").default} />
                        </span>
                        Leads  / Clients
                      </button>
                    </h2>
                    <div id="flush-collapseTwo" className="accordion-collapse collapse " aria-labelledby="flush-headingTwo" data-bs-parent="#accordionFlushExample">
                      <div className="">
                        <ul style={{ paddingLeft: "0px", width: "100%" }} className="hello">
                          <li className="">
                            <Link to="/clientTodo" className="nav-link link-dark  fontStylingBar">
                              <span className="pe-2">
                                <img src={require("../images/list-text.svg").default} />
                              </span>
                              To do / Non traité / Attente
                            </Link>
                          </li>
                          <li className="">
                            <Link to="/clientProgress" className="nav-link link-dark  fontStylingBar">
                              <span className="pe-2">
                                <img src={require("../images/analytics.svg").default} />
                              </span>
                              En cours de recherche
                            </Link>
                          </li>
                          <li className="">
                            <Link to="/clientContract" className="nav-link link-dark  fontStylingBar">
                              <span className="pe-2">
                                <img src={require("../images/contractList.svg").default} />
                              </span>
                              Terminé / Contrat en cours
                            </Link>
                          </li>
                          <li className="">
                            <Link to="/archived" className="nav-link link-dark fontStylingBar"> <span className="pe-2">
                              <img src={require("../images/archivedList.svg").default} />
                            </span>Annulé / Archivé
                            </Link>
                          </li>
                        </ul>
                      </div>
                    </div>
                  </div>
                  <div className="accordion-item">
                    <h2 className="accordion-header" id="flush-headingThree">
                      <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#flush-collapseThree" aria-expanded="false" aria-controls="flush-collapseThree">
                        <span className="pe-2">
                          <img src={require("../images/employeeicon.svg").default} />
                        </span>
                        Candidats / Employés
                      </button>
                    </h2>
                    <div id="flush-collapseThree" className="accordion-collapse collapse" aria-labelledby="flush-headingThree" data-bs-parent="#accordionFlushExample">
                      <div className="">
                        <ul style={{ paddingLeft: "0px", width: "100%" }}>
                          <li className="">
                            <Link to="/todolist" className="nav-link link-dark fontStylingBar">
                              <span className="pe-2">
                                <img src={require("../images/list-text.svg").default} />
                              </span>
                              En sommeil
                            </Link>
                          </li>
                          <li className="">
                            <Link to="/preSelected" className="nav-link link-dark fontStylingBar">
                              <span className="pe-2">
                                <img src={require("../images/todoList.svg").default} />
                              </span>
                              Pre-Selected
                            </Link>
                          </li>

                          <li className="">
                            <Link to="/embauchlist" className="nav-link link-dark fontStylingBar">
                              <span className="pe-2">
                                <img src={require("../images/contractList.svg").default} />
                              </span>Embauché

                            </Link>
                          </li>
                          <li className="">
                            <Link to="/archivedlist" className="nav-link link-dark fontStylingBar">
                              <span className="pe-2">
                                <img src={require("../images/archivedList.svg").default} />
                              </span>
                              Archivé
                            </Link>
                          </li></ul>
                      </div>
                    </div>
                  </div>
                  <div className="accordion-item">
                    <h2 className="accordion-header" id="flush-headingOne">
                      <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#flush-collapseOne" aria-expanded="false" aria-controls="flush-collapseOne">
                        <span className="pe-2">
                          <img src={require("../images/settings.svg").default} />
                        </span>
                        Manage
                      </button>
                    </h2>
                    <div id="flush-collapseOne" className="accordion-collapse collapse " aria-labelledby="flush-headingOne" data-bs-parent="#accordionFlushExample">
                      <div className="">
                        <ul style={{ paddingLeft: "0px", width: "100%" }} >  <li className="">
                          <Link to="/addNewSector" className="nav-link link-dark fontStylingBar" aria-current="page">
                            <span className="pe-2">
                              <img src={require("../images/addsector.svg").default} />
                            </span>
                            Add New Sector
                          </Link>
                        </li>
                          <li className="">
                            <Link to="/userList" className="nav-link link-dark fontStylingBar" aria-current="page">
                              <span className="pe-2">
                                <img src={require("../images/adduser.svg").default} />
                              </span>
                              User List
                            </Link>
                          </li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </li>
                <li style={{ border: '0px' }} className="list-group-item" onClick={(e) => LogOut()}>
                  <Link to="/" className="signOut" aria-current="page">
                    <span className="pe-2">
                      <img className="logoutImage" src={require("../images/logout.svg").default} />
                    </span>
                    Sign Out
                    <Toaster
                      position="top-right"
                    />
                  </Link>
                </li>
              </ul>
            </div>
          </div>

          <div className="col-10 contentDiv" style={{ float: 'right' }}>
            <section>
              {props.children}
            </section >
          </div>
        </div>




      </div>

    </>
  );
}
export default Sidebar;
